import debounce from 'lodash.debounce';

let open_modal = document.querySelectorAll('.open_modal');
let close_modal = document.getElementById('close_modal');
let modal = document.getElementById('modal');
let body = document.getElementsByTagName('body')[0];
let modalText = document.querySelector('.modal_txt');

for (let i = 0; i < open_modal.length; i++) {
  open_modal[i].addEventListener('click', debounce(openModalWindow, 2000)); //   
}
close_modal.addEventListener('click', closeModalWindow); //   

function closeModalWindow() {
  modal.classList.add('bounceOutDown'); //   
  window.setTimeout(function () {
    //     (   ).
    modal.classList.remove('modal_vis');
    body.classList.remove('body_block'); //  
  }, 500);
}

function openModalWindow() {
  modal.classList.add('modal_vis'); //   
  modal.classList.remove('bounceOutDown'); //   
  body.classList.add('body_block'); //  
  modalText.innerHTML =
    '<p> ,  !?    !?      GoIT!</p>'
}

body.addEventListener('click', debounce(openModalWindowWithTimer, 2000));

let counter = 0;
let enteredName = '';
let text = '';

function openModalWindowWithTimer(e) {
  const target = e.target;
  if (target === close_modal) {
    counter += 1;
    if (enteredName.trim().length >= 2) {
      return;
    }
    if (counter === 1) {
      text =
        '<p class="modal-first-paragraph">!    !     GoIT!     FrontEnd,        .</p><label class="modal-first-paragraph" for="name">    .   :</label><input type="text" id="name" name="name" size="18"><button class="modal-button" type="submit"></button>';
      openModalWindowSecond(text);
      const modalInput = document.querySelector('#name');
      modalInput.addEventListener('input', queryName);
      const buttonModal = document.querySelector('.modal-button');
      buttonModal.addEventListener('click', renderModal);
    } else if (counter > 1) {
      text =
        '<p class="modal-first-paragraph">    .   ,       </p><label for="name">    :</label><input type="text" id="name" name="name" size="18"><button class="modal-button" type="submit"></button>';
      openModalWindowSecond(text);
      const modalInput = document.querySelector('#name');
      modalInput.addEventListener('input', queryName);
      const buttonModal = document.querySelector('.modal-button');
      buttonModal.addEventListener('click', renderModal);
    }
  }
}

function queryName(e) {
  enteredName = e.target.value;
  return enteredName;
}

function renderModal() {
  if (enteredName.trim().length >= 2) {
    close_modal.removeEventListener('click', closeModalWindow);
    setTimeout(renderOk, 500);
    setTimeout(renderComp, 3000);
    setTimeout(renderPhoto, 7000);
    setTimeout(renderText, 11000);
    setTimeout(addApology, 13000);
    setTimeout(close_modal.addEventListener('click', closeModalWindow), 14500);
    counter = 0;
    // enteredName = '';
  } else if (enteredName.trim() === '') {
    text =
      '<p class="modal-first-paragraph"> ,    ,      ...</p><label for="name">   :</label><input type="text" id="name" name="name" size="18"><button class="modal-button" type="submit"></button>';
    openModalWindowSecond(text);
    const modalInput = document.querySelector('#name');
    modalInput.addEventListener('input', queryName);
    const buttonModal = document.querySelector('.modal-button');
    buttonModal.addEventListener('click', renderModal);
  } else if (enteredName.trim().length === 1) {
    text =
      '<p class = "modal-first-paragraph"> ,       .   .</p><label for="name">   :</label><input type="text" id="name" name="name" size="18"><button class="modal-button" type="submit"></button>';
    openModalWindowSecond(text);
    const modalInput = document.querySelector('#name');
    modalInput.addEventListener('input', queryName);
    const buttonModal = document.querySelector('.modal-button');
    buttonModal.addEventListener('click', renderModal);
  }
}

function openModalWindowSecond(text) {
  modal.classList.add('modal_vis'); //   
  modal.classList.remove('bounceOutDown'); //   
  body.classList.add('body_block'); //  
  modalText.innerHTML = text;
}

function renderOk() {
  modalText.innerHTML =
    '<p> ,      ...</p><img class="modal-photo" src="https://cdn.falauniversidades.com.br/wp-content/uploads/2020/06/17122042/Anonymous.jpg">';
}

function renderComp() {
  modalText.innerHTML =
    '<p>          .    ...</p><img class="modal-photo" src="https://cdn.falauniversidades.com.br/wp-content/uploads/2020/06/17122042/Anonymous.jpg">';
}

function renderPhoto() {
  modalText.innerHTML =
    '<p>!     !  . --,    ...</p><img class="modal-photo" src="https://cdn.falauniversidades.com.br/wp-content/uploads/2020/06/17122042/Anonymous.jpg">';
}

function renderText() {
  modalText.innerHTML =
    '<p>    :</p><img class="modal-photo" src="https://scontent.fiev19-1.fna.fbcdn.net/v/t1.6435-9/fr/cp0/e15/q65/60248006_2181999468521956_492403444842233856_n.jpg?_nc_cat=102&ccb=1-3&_nc_sid=7aed08&efg=eyJpIjoidCJ9&_nc_ohc=cyGLcmoKX9IAX-VMeW1&_nc_ht=scontent.fiev19-1.fna&tp=14&oh=784a4bdad518cce57586510f8d7871a7&oe=60CFDD36">';
}

function addApology() {
  modalText.insertAdjacentHTML(
    'beforeend',
    '<p>, ,  !? ,     ...   ,    . ,          !? )))</p>',
  );
}